
.baseMainContentContainer {
  height: 100vh;
  width: 100vw;
  background-color: #f8f8f8;
  display: flex;
  overflow: hidden;
} 



  #pageTitle {
    font-size: 30pt;
    color: orange;
    display: inline-block;

  }

  .contentContainer {
    display: flex;
    flex-direction: column;
    flex: 1;
    background: #f8f8f8;
    overflow-y:scroll
  }

  .margin {
    margin-left: 3vw;
    margin-right: 3vw;
  }

  .headContent {
    flex: 1;
    display: flex;
    flex-direction: column;
    /* background-color: green; */
    align-items: center;
    justify-content: center;
  }

  .mainContent {
    flex: 8;
    display: flex;
    flex-direction: column;
    
    /* background-color: red; */
  }

  .footerContent {
    flex: 1;
    /* background-color: blue; */
  }

  .Sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    -webkit-app-region: drag;
    align-self: flex-start;
    /* width: 130px; */
    height: 100vh;
    z-index: 2;
    /* background: transparent; */
    background: #ffffff;
    border-right: 0.2px solid #abababba;
    padding: 20px;
    filter: drop-shadow(1px 1px 0.5px rgba(103, 100, 100, 0.25));
  }

  #burgerMenyIcon {
    width: 30px;
    position: fixed;
    left: 20px;
    top: 20px;
    display: none;
  }

  @media (max-width: 600px) { 

    .Sidebar {
      display: none;
    }

    #burgerMenyIcon {
      display: block;
    }

    .mobileMenu {
      margin-top: 100px;
      width: 100vw;
      height: 100vh;
      display: flex;
      justify-content: center;
    }
    
    #baseMainContentContainer {
      flex-direction: column;
    }

    #headerContentContainer {
      align-self: flex-start;
    }

    #pageTitle {
      margin-left: 50px;
      margin-top: 20px;
      font-size: 1.2em;
    }
  }
